
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {feedback} from '../config'
export default {
  head: {
    title: {
      inner: ''
    }
  },
  components: {
    Group,
    Cell,
    XInput,
    XTextarea,
    XButton
  },
  data () {
    return {
      loading: false,
      form: {
        userId: '',
        content: '',
        phone: ''
      }
    }
  },
  mounted () {
    this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
  },
  methods: {
    handleSubmit () {
      this.$http({
        method: 'jsonp',
        url: feedback,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form,
        before: () => {
          this.loading = true
        }
      })
      .then(res => {
        console.log(res)
        this.loading = false
        this.form.content = this.form.phone = ''
        if (res.body.status) {
          this.$vux.toast.show({
            type: 'text',
            width: '12em',
            position: 'bottom',
            text: '',
            time: '1000'
          })
        } else {
          this.$vux.toast.show({
            type: 'text',
            width: '12em',
            position: 'bottom',
            text: res.body.msg,
            time: '1000'
          })
        }
      })
    }
  }
}



